.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 3px rgba(63, 63, 63, 0.822);
    padding: 16px;
    left: 15%;
    border-radius: 5px;
    top: 30%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    align-items: center;
    text-align: center;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}